.app-loader {
  background: #eff8f2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 99999;
  color: #153031;
  text-align: center;
  transition: all 0.2s;
}

.app-loader.hide {
  opacity: 0;
}

.app-loader__container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.app-loader__title {
  font-size: 40px;
  line-height: 48px;
}

.app-loader__subtitle {
  font-size: 20px;
  line-height: 24px;
}

.homescreen {
  padding-top: 80px;
}

.homescreen .card {
  box-shadow: 0px 20px 80px rgba(58, 172, 89, 0.16);
}

.navbar-icon {
  width: 30px;
  height: 24px;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto 4px;
  transition: all 0.25s;
}

.navbar-icon.home {
  background-size: 27px;
  background-image: url("./home.svg");
}

.nav-link.active .navbar-icon.home {
  background-image: url("./home_active.svg");
}

.navbar-icon.connect {
  background-size: 24px;
  background-image: url("./connect.svg");
}

.nav-link.active .navbar-icon.connect {
  background-image: url("./connect_active.svg");
}

.navbar-icon.calculator {
  background-size: 32px;
  background-image: url("./calculator.svg");
}

.nav-link.active .navbar-icon.calculator {
  background-image: url("./calculator_active.svg");
}

.navbar-icon.chat {
  background-size: 30px;
  background-image: url("./chat.svg");
}

.nav-link.active .navbar-icon.chat {
  background-image: url("./chat_active.svg");
}

.nav-link {
  font-size: 14px;
  line-height: 17px;
  color: rgba(58, 172, 89, 0.8);
  text-transform: uppercase;
}

.nav-link.active {
  font-family: "Neometric Bold";
  color: black !important;
}

.h1-title {
  font-size: 40px;
  line-height: 48px;
  font-family: "Neometric Bold";
}

.card-content {
  font-size: 18px;
  line-height: 22px;
  padding-top: 32px;
}

.card-subcontent {
  padding-top: 16px;
  font-size: 14px;
  line-height: 17px;
  color: rgba(0, 0, 0, 0.5);
}

.btn.btn-green,
.btn.btn-green:focus,
.btn:hover {
  background: rgb(58, 172, 89);
  color: white !important;
}

.btn:active {
  background: #3d8b53 !important;
  color: white;
}

.card {
  border-radius: 24px;
  border: 4px solid #eff8f2;
  box-sizing: border-box;
}

.dot {
  border-radius: 50%;
  width: 4px;
  height: 4px;
  background: #3aac59;
  margin: 0 auto 6px;
}

.btn {
  border-radius: 54px;
  padding: 20px 0;
  border: 1px solid rgba(58, 172, 89, 0.5);
  color: #3aac59;
  width: 100%;
  font-family: "Neometric Bold";
}

.btn.btn-sm {
  width: auto;
  padding: 4px 8px;
}

.text-green {
  color: #3aac59;
}

.modal-num {
  font-size: 18px;
  line-height: 22px;
  color: #3aac59;
}

.modal-text {
  font-size: 18px;
  line-height: 22px;
}

.modal-input {
  background: rgba(0, 0, 0, 0.08);
  border-radius: 24px;
  border: 1px solid transparent !important;
  transition: all 0.2s;
}

.modal-input[disabled] {
  background: rgba(0, 0, 0, 0.08) !important;
}

input[type="text"]:hover {
  background: rgba(0, 0, 0, 0.12);
}

input[type="number"] {
  border: 1px solid transparent !important;
}

input[type="number"]:hover {
  border: 1px solid rgba(58, 172, 89, 0.24) !important;
}

input[type="number"]:focus,
input[type="text"]:focus {
  background: white !important;
  border: 1px solid #3aac59 !important;
}

/* Style range */
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: 4px solid #3aac59;
  height: 40px;
  width: 40px;
  border-radius: 50% !important;
  background: #ffffff url("./range.svg") no-repeat 50% 50%;
  cursor: pointer;
  margin-top: -15px;
}

/* All the same stuff for Firefox */
input[type="range"]::-moz-range-thumb,
input[type="range"]::-ms-thumb {
  border: 4px solid #3aac59;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background: #ffffff url("./range.svg") no-repeat 50% 50%;
  cursor: pointer;
}

input[type="range"]:active::-webkit-slider-thumb,
input[type="range"]:focus::-webkit-slider-thumb {
  outline: none;
  box-shadow: 0px 0px 0px 8px rgba(58, 172, 89, 0.12);
  background: #3aac59 url("./range_hover.svg") no-repeat 50% 50%;
}

input[type="range"]:active::-moz-range-thumb,
input[type="range"]:active::-ms-thumb,
input[type="range"]:focus::-moz-range-thumb,
input[type="range"]:focus::-ms-thumb {
  outline: none;
  box-shadow: 0px 0px 0px 8px rgba(58, 172, 89, 0.12);
  background: #3aac59 url("./range_hover.svg") no-repeat 50% 50%;
}

input[type="range"]::-webkit-slider-runnable-track {
  background: rgba(58, 172, 89, 0.12);
}

input[type="range"]::-moz-range-track {
  background: rgba(58, 172, 89, 0.12);
}

.text-small {
  font-size: 12px;
  line-height: 14px;
}

.form-select {
  border-radius: 24px;
  border: 1px solid transparent;
}

.form-select:hover {
  border: 1px solid rgba(58, 172, 89, 0.24);
}

.form-select:active {
  border: 1px solid #3aac59;
}

.navbar > div {
  padding-bottom: env(safe-area-inset-bottom);
}

.form-control {
  border: 0;
  border-radius: 24px;
}

.community-link {
  color: #3aac59;
  text-decoration: none;
}

.community-link img {
  margin: 0 auto;
  transition: all 0.23s;
}

.modal-header,
.modal-footer {
  border: 0 !important;
}

.container {
  max-width: 960px !important;
}

/* Desktop only */
@media (min-width: 768px) {
  .nav-link.active span {
    border-bottom: 1px solid #3aac59;
  }

  .homescreen .h1-title {
    margin-bottom: 60px;
  }

  .h1-title {
    text-align: center;
    margin-top: 80px;
    padding-top: 0 !important;
  }

  .modal-body .h1-title {
    margin-top: 0;
    text-align: left;
  }

  .dot {
    width: 8px;
    height: 8px;
    margin: 0 6px;
  }

  .modal-content {
    border-radius: 24px;
    border: 0 !important;
  }

  .btn {
    width: 240px;
  }

  .advantages + .advantages {
    border-left: 1px solid rgba(196, 196, 196, 0.2);
  }

  .calculator-screen {
    padding-top: 1px !important;
    background: linear-gradient(
      to bottom,
      rgba(58, 172, 89, 0.08),
      rgba(58, 172, 89, 0.08) 60%,
      #fff 60%,
      #fff 100%
    ) !important;
  }

  #calculator {
    padding-bottom: 40px;
  }

  #calculator + div {
    text-align: center;
  }

  #profitRange {
    margin: 30px 0 !important;
  }

  .profitInput {
    width: 50%;
    margin: 0 auto;
  }

  .profitInput input {
    max-width: 120px;
    font-size: 1.5rem;
  }

  .profitInput .form-select {
    max-width: 80px !important;
  }

  .profitBtn {
    margin-top: 24px;
  }

  .communityText {
    text-align: center;
  }

  .modal-footer {
    justify-content: stretch;
  }

  .modal-footer .btn {
    padding: 15px 0;
  }

  .advantages img {
    max-width: 200px;
    max-height: 200px;
  }

  .card-body {
    padding-top: 2.5rem;
  }

  .card-body h5 {
    padding-top: 0 !important;
  }

  .navbar-community {
    position: absolute;
    right: 40px;
    top: 0;
    bottom: 0;
    align-items: center;
    justify-content: flex-end;
  }

  .community-link:hover img {
    transform: scale(1.25);
  }
}

/* Mobile only */
@media (max-width: 768px) {
  .homescreen-title {
    padding-top: 40px;
    margin-bottom: 20px;
  }

  .nav-link {
    font-size: 12px;
    line-height: 14px;
    color: rgba(58, 172, 89, 0.8);
    text-transform: none;
  }

  .homescreen {
    padding-top: 40px;
  }

  .modal-footer {
    justify-content: center;
  }

  .advantages img {
    max-width: 96px;
    max-height: 96px;
  }

  .app-loader__subtitle {
    margin-left: auto;
    margin-right: auto;
    max-width: 180px;
  }
}
